.list {
	display: flex;
	justify-content: space-between;
	width: 68.4%;
	list-style: none;
	color: #FFF;
	font-size: 16px;
	font-weight: bold;
	line-height: 2.2;
	margin-right: 20px;
	&__item {
		display: inline-block;
	}
	&__link {
		text-decoration: none;
		color: #fff;
		&:hover {
			text-shadow: 1px 1px 5px #000, 0 0 2em #fff;
		}
	}
}
