.content {
   align-items: flex-start;
   padding: 17px 23px;
   background-color: #fff;
}
.left-sidebar {
   width: 13.3rem;
   &__headline {
      font-size: 18px;
      margin-bottom: 5px;
   }
   &__lorem {
      color: #5c646e;
      font-size: 12px;
      line-height: 1.7;
      margin-bottom: 7px;
   }
   &__link {
      color: #95161f;
      font-size: 12px;
   }
}
.right-sidebar {
   width: 43.5rem;
   .lists__type {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
   }
   .topic {
      &__headline {
         margin-bottom: 6px;
      }
      &__lorem {
         font-size: 14px;
         color: #5c646e;
         line-height: 1.43;
         margin-bottom: 13px;
      }
   }
   .direction {
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 30px;
      &__item {
         margin-right: 16px;
         width: 28%;
      }
      &__headline {
         font-size: 18px;
         font-weight: bold;
         margin-bottom: 10px;
      }
      &__img {
         // height: 147px;
         // width: 97%;
         border: 1px solid white;
         box-shadow: 0 5px 5px rgba(0, 0, 0, 0.3);
         > img {
            display: flex;
         }
      }
   }
   .lists {
      &__headline {
         margin-bottom: 13px;
      }
      &__name {
         color: #5c646e;
         font-size: 14px;
         margin-bottom: 4px;
      }
      &__ul,
      &__unstyled,
      &__ol {
         font-size: 14px;
         color: #6b6f74;
         line-height: 1.6;
         margin-bottom: 30px;
      }
      &__type {
         &_circle,
         &_unstyled,
         &_ordered {
            width: 30%;
         }
      }
      &__unstyled,
      &__ol {
         list-style: none;
         margin: 0;
         padding: 0;
      }
   }
   ul li ul > li {
      list-style-type: disc;
      padding-left: 1rem;
      margin: 0;
   }
   .mnemonic {
      font-size: 12px;
      color: #5c646e;
      background-color: #dedede;
      border-radius: 2px;
   }
}
